import DataGrid, { GroupItem, Scrolling, Selection, Summary, TotalItem, FilterRow, HeaderFilter, Sorting, Paging } from 'devextreme-react/data-grid';
import DataSource from 'devextreme/data/data_source';
import React from 'react';
import { connect } from 'react-redux';
import esMessages from 'devextreme/localization/messages/es.json';
import { locale, loadMessages } from 'devextreme/localization';
import { setChartsData, setSortColumns} from '../storage/actions';
import config from "devextreme/core/config";
import LinkCell from "./TableCells/Link";
import CurrencyCell from "./TableCells/Currency";
import Actions from "./TableCells/Actions/ActionsFunction";
import { Template } from "devextreme-react/core/template";
import TableFooter from './TableFooter';
import { fetchPreviewData as fetchPreviewDataAction, fetchData as RawFetchData } from '../storage/thunkActions';
import { getUserToken, UtilManager } from '../Utils';
import {store} from '../index';
// import cloneDeep from 'lodash/cloneDeep';

import isEqual from 'lodash/isEqual';
import CustomStore from 'devextreme/data/custom_store';
config({
  defaultCurrency: 'USD'
});

class Table extends React.Component {
  
  constructor(props) {
    super(props);
    locale('es');
    loadMessages(esMessages);
    this.state = {
      table_columns: [],
      footer_columns: [],
      group_footer_columns: [],
      modules_by_path: {},
      loadingTag: ""
    }

    this.customStore = null;
  }


  componentDidMount() {    
    UtilManager.GetResource("Custom.SDKFlex.Cargando").then((res) => {
      this.setState({loadingTag: res});
    });
    let self = this;
    this.customStore = new CustomStore({
      key: "rowid",
      load: async function (loadOptions) {
        let current_state = store.getState().general || {};
        var skip = loadOptions.skip || 0;
        var take = loadOptions.take || 100;
        let url = '/api/BLFlex/PreviewData/'
        let params = {};
        let sort = loadOptions.sort || [];
        if(skip === 0 && sort.length === 0 && !current_state.change_filter_search){
          return current_state.preview_data;
        }
        const regularColumns = self.props.columns.filter(col => !col.customFn);
        if(sort.length>0){
          let index = regularColumns.findIndex(e => e.key_name === sort[0].selector);
          regularColumns[index].sortType = sort[0].desc ? "desc" : "asc";
        }
        //concatenar las columnas 
        params = {
            "module_path": current_state.report_header_data.module_path,
            "selected_class": current_state.report_header_data.selected_class,
            "module": current_state.report_header_data.module,
            "filters": current_state.filters,
            //"columns": current_state.columns,
            "columns": regularColumns,
            "groups": current_state.groups,
            "set_top": 'false',
            "skip": skip,
            "take": take,
            "filter_search": current_state.filter_search,
        }

        let fetchParams ={
            method: 'POST',
            headers: {
              "Accept": "application/json",
              // "X-CSRFToken": cookie.load('csrftoken'),
              "X-Auth-Token": getUserToken(),
              'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify(params)
        }

        var data = await RawFetchData(url, fetchParams, current_state);

        return {
          data
        };    
      }
    });
    

    if (typeof window["oreports_app_table_" + this.props.app_id+"_"+this.props.guid] === "undefined") {
      window["oreports_app_table_" + this.props.app_id+"_"+this.props.guid] = this;
    }

    if (!this.dataGridRef || !this.dataGridRef.instance) {
      return false;
    }

    this.props.table_filters.forEach(filter => {
      try {
        this.dataGridRef.instance.columnOption(filter[0], "filterValue", filter[2]);
      } catch (error) {
        console.log("error table_filters", error);
      }
    });

    this.getColumns(this.props);
  }

  componentWillReceiveProps(nextProps){
    if(!isEqual(nextProps.columns, this.props.columns) || !isEqual(nextProps.groups, this.props.groups) || !isEqual(nextProps.report_header_data.module_path, this.props.report_header_data.module_path) || !isEqual(nextProps.report_header_data.selected_class, this.props.report_header_data.selected_class) || !isEqual(nextProps.is_create_edit, this.props.is_create_edit)){
      this.getColumns(nextProps);
    }
  }

  shouldComponentUpdate(new_props, new_state) {
    if (new_props.loading !== this.props.loading) {
      if (this.dataGridRef && this.dataGridRef.instance) {
        if (new_props.loading) {          
          this.dataGridRef.instance.beginCustomLoading(this.state.loadingTag);
        } else {
          this.dataGridRef.instance.endCustomLoading();
        }
      } 
    }
    if (!isEqual(new_props.table_filters, this.props.table_filters)) {
      if (!this.dataGridRef || !this.dataGridRef.instance) {
        return false;
      }

      new_props.table_filters.forEach(filter => {
        try {
          this.dataGridRef.instance.columnOption(filter[0], "filterValue", filter[2]);
        } catch (error) {
          console.log("error table_filters", error);
        }
        //this.dataGridRef.instance.filter([filter]);

      });
      return false;
    }
    return true;
  }

  onContentReady = (e) => {
    if (!this.dataGridRef || !this.dataGridRef.instance) {
      return;
    }

    let filterExpr = this.dataGridRef.instance.getCombinedFilter();
    if (!filterExpr) {
      this.props.setChartsData(this.props.preview_data);
      return;
    }

    const newGridDataSource = new DataSource({
      store: this.props.preview_data,
      paginate: false
    });


    if (!newGridDataSource) {
      return;
    }
    newGridDataSource.filter(filterExpr);
    let self = this;
    newGridDataSource.load().done(function (result) {
      self.props.setChartsData(result);
    });
  }

  getModuleName = async (path, module_path, selected_class) => {

    let module_name = this.state.modules_by_path[path];

    if (!module_name){
      return await fetch('/api/BLFlex/GetRelatedModuleByPath/', {
        method: 'POST',
        credentials: "same-origin",
        headers: {
          "Accept": "application/json",
          // "X-CSRFToken": cookie.load('csrftoken'),
          "X-Auth-Token": getUserToken(),
          'X-Requested-With': 'XMLHttpRequest'
        },
        body: JSON.stringify({
          "module_path": module_path,
          "selected_class": selected_class,
          "path": path,
        })
      }).then(res => {
        return res.json()
      })
        .then(res => {          
          return res.data.module_name;
        })
        .catch(error => {
          return false;
        });
    }else{
      return module_name;
    }
  }

  getColumns = async (props) => {
    const { columns, groups, is_list_view, is_create_edit} = props;
    const number_types = ['IntegerField', 'number', 'FloatField', 'CurrencyField', 'DecimalField'];    
    const all_columns = columns.concat(groups);
    let table_columns = [];
    let footer_columns = [];
    let group_footer_columns = [];
    if(is_list_view && !is_create_edit){
      let column_props = {
        minWidth: 200,
        dataField: "actions",
        caption: await UtilManager.GetResource("Action.Generic.Actions"),
        cellTemplate: "actions"        
      }

      table_columns.push(column_props);
    }

    for (let index = 0; index < all_columns.length; index++) {
      const column = all_columns[index];
      if(is_list_view && column.key_name === "rowid"){
        continue;
      }
      let column_props = {
        minWidth: 200,
        dataField: column.key_name,
        caption: column.label
      }

      if (number_types.indexOf(column['type']) > -1) {
        column_props['dataType'] = 'number';
      }

      if (column["type"] === 'CurrencyField') {
        column_props["cellTemplate"] = "currencyCell";
      } else if (column["type"] === 'FloatField') {
        column_props['format'] = { type: 'fixedPoint', precision: 2 };
      }else if (column["type"] === 'DecimalField') {
        column_props['format'] = { type: 'fixedPoint', precision: column.decimal_places || 2 };
      }else if (column["type"] === 'DateTimeField') {
        column_props['dataType'] = "datetime";
      } else if (column["type"] === 'DateField') {
        column_props['dataType'] = "date";
      }

      if(column['isSorter']){
        //column_props['defaultSortIndex']=1;
        //column_props['defaultSortOrder']="asc";
        column_props['sortIndex']=1;
        column_props['sortOrder']="asc";
      }
      

      let group_index = groups.findIndex((item) => item.key_name === column.key_name);

      if (((column["link"] && typeof column["link"] === "boolean") || column["link"] === 'True') && group_index === -1) { //Retrocompatibilidad, así se guarda en el OReports desde tiempos inmemoriales
        column_props["related_module_name"] = await this.getModuleName(column["path"], props.report_header_data.module_path, props.report_header_data.selected_class);
        column_props["cellTemplate"] = "linkCell";
      }

      if (group_index > -1) {
        column_props["groupIndex"] = group_index;
        column_props["cellTemplate"] = undefined;
      }
      
      table_columns.push(column_props);

      if (column.aggregate) {  //Retrocompatibilidad, así se guarda en el OReports desde tiempos inmemoriales
        let customizeText = undefined;
        if (column["type"] === 'CurrencyField' && column.aggregate !== 'count'){
          customizeText = (obj_data) => {
            let data = obj_data.value;
            if(data || data === 0){
                data = "$ " + data.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            }
            try {
              let ret = obj_data.valueText.replace(obj_data.value, data);
              return ret;
            } catch (error) {}
            
            return data;
          }
        }
        if (groups.length > 0) {
          group_footer_columns.push(<GroupItem
            key={"footer_group_" + column.key_name}
            column={column.key_name}
            showInGroupFooter={false}
            alignByColumn={true}
            summaryType={column.aggregate}
            valueFormat={column_props.format}
            customizeText={customizeText}/>);
        }
        footer_columns.push(<TotalItem
          key={"footer_" + column.key_name}
          column={column.key_name}
          showInColumn={true}
          summaryType={column.aggregate}
          valueFormat={column_props.format}
          customizeText={customizeText} />);
      }
    }
    this.setState({
      table_columns,
      footer_columns,
      group_footer_columns
    });
  }


  onOptionChanged = async(e) => {
    if(e.fullName.endsWith("ortOrder") && !this.props.server_pagination) {
      
      try {
        let getIndex = e.fullName.split(']')[0].substr(-1)  
        const col = this.props.columns[getIndex] || null;
        
        if(col){
          const values = {isSorter: true, sortType: e.value}
          this.props.setSortColumns(col, values);
        }
      } catch (error) {
        console.log('error=> ',error);
        
      }
    }
  }

  render() {
    const { table_columns, footer_columns, group_footer_columns} = this.state;
    const { preview_data, take, server_pagination } = this.props;
    const remoteOperations = {};
    const data_source = server_pagination  ? this.customStore : new DataSource({
        store: preview_data,
        paginate: false
      });

    let mode = "virtual";

    if(server_pagination){
      remoteOperations['paging'] = true;
      remoteOperations['sorting'] = true;

      mode = "infinite";
    }
    return (
      <div className={"table-container" + (this.props.hidden ? ' hidden' : '')}>
        <DataGrid
          elementAttr={{
            id: 'tableContainer'
          }}
          ref={(c) => this.dataGridRef = c}
          columns={table_columns}
          dataSource={data_source}
          onOptionChanged={this.onOptionChanged}
          allowColumnReordering={false}
          showBorders={false}
          rowAlternationEnabled={true}
          columnAutoWidth={true}
          onContentReady={this.onContentReady}
          wordWrapEnabled={true}
          remoteOperations={remoteOperations}
        >
          <Sorting mode="single"/>
          {server_pagination ? <Paging defaultPageSize={take} /> : null}
          <Template name={"linkCell"} render={LinkCell} >
          </Template>
          <Template name={"currencyCell"} render={CurrencyCell} >
          </Template>
          <Template name={"actions"} render={Actions}>
          </Template>
          <FilterRow visible={true} />
          <HeaderFilter visible={true} />
          <Scrolling mode={mode} />
          <Selection mode="single" />
          {preview_data.length > 0 ?
            <Summary>
              {footer_columns}
              {group_footer_columns}
            </Summary> : null}
        </DataGrid>
        {!server_pagination ? <TableFooter /> : null}        
      </div>

    );
  }
}

const mapStateToProps = state => {
  return {
    'columns': state.general.columns,
    'groups': state.general.groups,
    'preview_data': state.general.preview_data,
    'loading': state.general.loading,
    'is_create_edit': state.general.is_create_edit,
    'table_filters': state.general.table_filters,
    'app_id': state.general.app_id,
    'report_header_data': state.general.report_header_data,
    'is_list_view': state.general.is_list_view,
    'take': state.general.take,
    'server_pagination': state.general.server_pagination,
    'filter_search': state.general.filter_search,
    'guid': state.general.guid,
  };
};


const mapDispatchToProps = (dispatch, getState) => {
  return {
    'setChartsData': (data) => {
      dispatch(setChartsData(data));
      
    },
    'setSortColumns': async(col,values) => {
      await dispatch(setSortColumns(col,values));
      dispatch(fetchPreviewDataAction());
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Table);