import {
    fetchPreviewDataError, 
    fetchPreviewDataPending, 
    fetchPreviewDataSuccess, 
    getConfigPending,
    getConfigSuccess,
    getConfigError,
    saveDataError, 
    saveDataPending, 
    saveDataSuccess 
} from './actions';

import {
    formulasLib, getUserToken, SDKNotification
} from '../Utils'

export const save = (close_window = false) => {
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        dispatch(saveDataPending());
        fetch('/api/BLFlex/SaveReport/', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                "Accept": "application/json",
                // "X-CSRFToken": cookie.load('csrftoken'),
                "X-Auth-Token": getUserToken(),
                'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify({
                "id": current_state.id,
                "name": current_state.report_header_data.name,
                "description": current_state.report_header_data.description,
                "selected_class": current_state.report_header_data.selected_class,
                "module":  current_state.report_header_data.module,
                "visible_users": current_state.report_header_data.visible_users,
                "visible_roles": current_state.report_header_data.visible_roles,
                "visible_on_module": current_state.report_header_data.visible_on_module,
                "module_path": current_state.report_header_data.module_path,
                "select_list": current_state.columns,
                "filter_list": current_state.filters,
                "chart_list": current_state.chart_list,
                "owner": { id: current_state.report_header_data.owner_id },
                "owner_team": { id: current_state.report_header_data.owner_team_id },
                "group_list": current_state.groups,
                "version": 2,
                "metadata": JSON.stringify(Object.assign(current_state.report_header_data.metadata || {}, {
                    field_list: current_state.field_list,
                })),
                "is_list_view": current_state.is_list_view,
                "bl_name_list": current_state.bl_name_list,
                "columns_changed": current_state.columns_changed,
                'viewdef_name': current_state.viewdef_name,
                'delta_old': current_state.report_header_data.delta,
            })
        })
            .then(res => res.json())
            .then(res => {
                if (res.status === false) {
                    throw (res.message);
                }
                dispatch(saveDataSuccess(res.data));
                SDKNotification.ShowSuccess("BLFlex.Notification.SuccessSaveData");
                if (close_window) {
                    let new_url = '/BLFlex/detail/' + res.data.id;
                    if(window.SDKDotNetHelper != null && typeof window.SDKDotNetHelper != "undefined"){
                        window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", new_url);
                    }else{
                        window.location.href = new_url;
                    }
                }
                return res.data;
            })
            .catch(error => {
                try {
                    console.error(error);
                    SDKNotification.ShowError("Custom.Generic.Message.Error");
                } catch (err) {
                    console.error(err);
                    SDKNotification.ShowError("BLFlex.Notification.ErrorSave");
                }

                dispatch(saveDataError(error));
            })
    };
}

export const fetchMainConfig = () => {
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        let url = '/api/BLFlex/GetConfig/';
        dispatch(getConfigPending());
        fetch(url, {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                "Accept": "application/json",
                // "X-CSRFToken": cookie.load('csrftoken'),
                "X-Auth-Token": getUserToken(),
                'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify({
                "id": current_state.id || 0,
                "bl_name_list": current_state.bl_name_list || '',
                "viewdef_name": current_state.viewdef_name || '',
            })
        })
            .then(res => res.json())
            .then(res => {
                if (res.status === false) {
                    throw (res.message);
                }
                //console.log(res.data);
                //check if res.data.metadata is a string or object
                try {
                    if (typeof res.data.report_header_data.metadata === 'string') {
                        res.data.report_header_data.metadata = JSON.parse(res.data.report_header_data.metadata);
                    }
                } catch (error) {
                    console.log(error);
                }
                
                dispatch(getConfigSuccess(res.data));
                dispatch(fetchPreviewData());
                return res.data;
            })
            .catch(error => {
                try {
                    console.error(error);                    
                    SDKNotification.ShowError("Custom.Generic.Message.Error");
                } catch (err) {
                    console.error(err);                                        
                    SDKNotification.ShowError("Custom.Generic.Message.Error");
                }

                dispatch(getConfigError(error));
            });
    }
}

export const fetchData = (url, params, current_state, dispatch) => {    
    return fetch(url, params)
    .then(res => res.json())
    .then(res => {
        if (res.status === false) {
            throw (res.message);
        }
        if (res.data.length == 0) {
            if(dispatch != null){
                dispatch(fetchPreviewDataSuccess(res.data));
            }
            return res.data;
        }
        const fieldsMapTypes = current_state.field_list[0].options.map(option => Object.assign({}, { [option.name]: option.type })).reduce((obj, curr) => {
            return { ...obj, ...curr }
        }, {})
        const formulaColumns = current_state.columns.filter(col => col.customFn).concat(current_state.groups.filter(col => col.customFn))
        const calculatedColumnsNames = formulaColumns.map(col => col.key_name)
        const calculatedColumnsFormulas = formulaColumns
            .map(col => Object.assign({}, { [col.key_name]: col.formula }))
            .reduce((obj, curr) => {
                return { ...obj, ...curr }
            }, {})

        let contextAsScope = item => () => {
            return {
                ...formulasLib,
                ...item
            }
        }
        //columna tipo customFn, es necesario reemplazar los operandos en la formula y evaluar
        if (res.status === false) {
            throw (res.message);
        }

        const normalizeFormula = (formula) => {
            return formula.replace(/props\((.*?)\)/g, function (not_use, operand) {
                if (calculatedColumnsNames.indexOf(operand) === -1) {
                    if ((operand in fieldsMapTypes) && ["DecimalField", "FloatField"].indexOf(fieldsMapTypes[operand]) !== -1) {
                        return `(parseFloat(${operand} || 0))`
                    } else {
                        return `(${operand})`
                    }
                } else {
                    return `(${normalizeFormula(calculatedColumnsFormulas[operand], true)})`
                }
            })
        }

        const parseRes = res.data.map(item => {

            const calculatedColumns = formulaColumns.map(col => {
                const itemContext = contextAsScope(item)()
                return { [col.key_name]: (new Function(...Object.keys(itemContext), `return ${normalizeFormula(col.formula)}`))(...Object.values(itemContext)) }
            }).reduce((obj, curr) => {
                return { ...obj, ...curr }
            }, {})

            return { ...item, ...calculatedColumns }
        });

        if(dispatch != null){
            dispatch(fetchPreviewDataSuccess(parseRes));
        }        
        return parseRes
    }).catch(error => {                    
        console.error(error);
        try {
            SDKNotification.ShowError(error);                        
        } catch (err) {
            console.error(err);
            SDKNotification.ShowError("BLFlex.Notification.ErrorLoadData");
        }

        if(dispatch != null){
            dispatch(getConfigError(error));
        }else{
            return error;
        }
    })
    .catch(error => {
        console.error(error);
        try {
            SDKNotification.ShowError("Custom.Generic.Message.Error");
        } catch (err) {
            console.error(err);
            SDKNotification.ShowError("BLFlex.Notification.ErrorLoadData");
        }
        if(dispatch != null){
            dispatch(getConfigError(error));
        }else{
            return error;
        }
    })
    .catch(error => {
        if(dispatch != null){
            dispatch(fetchPreviewDataError(error));
        }else{
            return error;
        }            
    })
}

export const fetchPreviewData = () => {
    return (dispatch, getState) => {        
        let current_state = getState().general || {};
        if (current_state.timeout_data_id !== null && typeof (current_state.timeout_data_id) !== "undefined") {
            clearTimeout(current_state.timeout_data_id);
        }
        let url = '';
        let params = {};
        if (current_state.is_create_edit && !current_state.is_list_view) {
            url = '/api/BLFlex/PreviewData/';
            //extraer las opciones del field_list en forma segura
            const regularColumns = current_state.columns.filter(col => !col.customFn)
            //concatenar las columnas 
            params = {
                "module_path": current_state.report_header_data.module_path,
                "selected_class": current_state.report_header_data.selected_class,
                "module": current_state.report_header_data.module,
                "filters": current_state.filters,
                //"columns": current_state.columns,
                "columns": regularColumns,
                "groups": current_state.groups,
                "set_top": 'true'
            }
        } else if(current_state.is_list_view){
            url = '/api/BLFlex/PreviewData/';
            //extraer las opciones del field_list en forma segura
            const regularColumns = current_state.columns.filter(col => !col.customFn)
            current_state.id = current_state.report_header_data.id;
            //concatenar las columnas 
            params = {
                "module_path": current_state.report_header_data.module_path,
                "selected_class": current_state.report_header_data.selected_class,
                "module": current_state.report_header_data.module,
                "filters": current_state.filters,
                //"columns": current_state.columns,
                "columns": regularColumns,
                "groups": current_state.groups,
                "set_top": 'false',
                "filter_search": current_state.filter_search,
            }
            if(current_state.server_pagination){
                params.skip = current_state.skip;
                params.take = current_state.take;
            }
        } else {
            url = '/api/BLFlex/UpdateData/';
            params = {
                "id": current_state.report_header_data.id,
                "filter_list": current_state.filters,
                'current_record': (current_state.current_record ? current_state.current_record : 0)
            }
        }
        let timeoutData = setTimeout(() => {
            fetchData(url, {
                method: 'POST',
                credentials: "same-origin",
                headers: {
                    "Accept": "application/json",
                    // "X-CSRFToken": cookie.load('csrftoken'),
                    "X-Auth-Token": getUserToken(),
                    'X-Requested-With': 'XMLHttpRequest'
                },
                body: JSON.stringify(params)
            }, current_state, dispatch);            
        }, 500);

        if(!current_state.is_list_view){
            dispatch(fetchPreviewDataPending(timeoutData));
        }else if(current_state.skip === 0){
            dispatch(fetchPreviewDataPending(timeoutData));
        }else if(!current_state.server_pagination){
            dispatch(fetchPreviewDataPending(timeoutData));
        }
    }
}