import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { applyMiddleware, createStore } from 'redux';
import { composeWithDevTools } from "redux-devtools-extension";
import thunk from 'redux-thunk';
import App from './App';
import * as serviceWorker from './serviceWorker';
import reducers from './storage/reducers';
import { arrayEval, getJSONAttribute, updateFieldKeyName, defaultJSONParse } from './Utils';


export let store;
window.mountOReportsReact = function (id) {

  const ELEMENT = document.getElementById(id);
  const columns = getJSONAttribute(ELEMENT, 'select_list', []);
  const chart_list = getJSONAttribute(ELEMENT, 'chart_list', []);
  const filters = getJSONAttribute(ELEMENT, 'filter_list', []);
  const base_obj_id = ELEMENT.getAttribute("base_obj_id") || '';
  const current_user_id = ELEMENT.getAttribute("current_user_id") || '';
  const current_view = ELEMENT.getAttribute("current_view") || '';
  const groups = getJSONAttribute(ELEMENT, 'groups', []);
  const modules_list = getJSONAttribute(ELEMENT, 'modules', []);
  const teams_list = getJSONAttribute(ELEMENT, 'teams', []); //TODO: Crear campo relacionado
  const users_list = getJSONAttribute(ELEMENT, 'users', []);
  const roles_list = getJSONAttribute(ELEMENT, 'roles', []);
  const operator_list = getJSONAttribute(ELEMENT, 'operator_list', []);
  const report_header_data = getJSONAttribute(ELEMENT, 'report_header_data', {});
  const chart_error = getJSONAttribute(ELEMENT, 'chart_error', {});
  const is_ajax = getJSONAttribute(ELEMENT, 'is_ajax', false);
  const ajax_show_all = getJSONAttribute(ELEMENT, 'ajax_show_all', false);
  const current_record = ELEMENT.getAttribute("current_record") || '';
  const is_list_view = ELEMENT.getAttribute("is_list_view")
  const bl_name_list = ELEMENT.getAttribute("bl_name_list")
  const viewdef_name = ELEMENT.getAttribute("viewdef_name")
  const guid_list_view = ELEMENT.getAttribute("guid_list_view")
  const can_edit_list_view = ELEMENT.getAttribute("can_edit_list_view")
  const can_delete_list_view = ELEMENT.getAttribute("can_delete_list_view")  
  const columns_changed = getJSONAttribute(ELEMENT, 'columns_changed', []);
  const skip = getJSONAttribute(ELEMENT, 'skip', '0');
  const take = getJSONAttribute(ELEMENT, 'take', '100');
  const server_pagination = ELEMENT.getAttribute("server_pagination");
  const filter_search = ELEMENT.getAttribute("filter_search");
  const guid = ELEMENT.getAttribute("guid");

  report_header_data.visible_users = arrayEval(report_header_data.visible_users);
  report_header_data.visible_roles = arrayEval(report_header_data.visible_roles);
  report_header_data.metadata = defaultJSONParse(report_header_data.metadata, {});

  if (!report_header_data.owner_id) {
    report_header_data.owner_id = current_user_id;
  }

  updateFieldKeyName(columns);
  updateFieldKeyName(filters);
  updateFieldKeyName(groups);



  let generalState = {
    id: base_obj_id,
    app_id: id,
    columns,
    groups,
    filters,
    modules_list,
    teams_list,
    users_list,
    roles_list,
    operator_list,
    report_header_data,
    field_list: (report_header_data.metadata && report_header_data.metadata.field_list ? report_header_data.metadata.field_list  : []),
    dragging: false,
    loading: false,
    preview_data: [],
    saving: false,
    show_config_modal: (base_obj_id ? false : true),
    current_view,
    is_create_edit: ['edit', 'create'].indexOf(current_view) !== -1,
    chart_list,
    table_filters: [],
    charts_data: [],
    chart_error,
    is_ajax,
    ajax_show_all,
    zoomed_chart_def: {},
    current_record,
    detail_name: (report_header_data.name && report_header_data.name ? report_header_data.name  : ''),
    is_list_view: (is_list_view === 'True' ? true : false),
    bl_name_list: bl_name_list,
    viewdef_name: viewdef_name,
    guid_list_view: guid_list_view,
    can_edit_list_view: (can_edit_list_view === 'True' ? true : false),
    can_delete_list_view: (can_delete_list_view === 'True' ? true : false),
    columns_changed,
    skip: parseInt(skip),
    take: parseInt(take),
    server_pagination: (server_pagination === 'True' ? true : false),
    filter_search,
    change_filter_search: false,
    guid,
  };

  let initialState = {
    general: generalState
  }
  const middlewares = [thunk];

  store = createStore(reducers, initialState, composeWithDevTools(applyMiddleware(...middlewares)));
  ReactDOM.render((
    <Provider store={store}>
      <App />      
    </Provider>), ELEMENT);
}

window.unmountOReportsReact = function (id) {
  const ELEMENT = document.getElementById(id);
  //ReactDOM.unmountComponentAtNode(ELEMENT);
  window.oreports_app_flexdebug = null;
  
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
