import React, { Component } from 'react';
import Select from 'react-select';
import { Form, FormGroup, Input, Label } from 'reactstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';
import { isValidDate } from '../../../Utils';
import ForeignKey from '../../Fields/ForeignKey';
import {getUserToken, UtilManager} from '../../../Utils';
import ResourceViewer from '../../Visualization/ResourceViewer';



// const Enum_yes =  UtilManager.GetResource("Enum.enumYesNo.Yes");
// const Enum_no =  UtilManager.GetResource("Enum.enumYesNo.No");

const boolean_options = [
    { value: "False", label: "No" },
    { value: "True", label: "Si" },
]
const date_fields_to = ['between'];
const date_fields_from_type_date = ['equal', 'not_equal', 'before', 'after', 'between'];
const date_fields_from_type_input = ['last_n_days', 'next_n_days'];



export class FieldOptions extends Component {

    state = {
        enum_options: []
    }

    componentDidMount() {
        const { field} = this.props;
        if (['SelectField', 'MultiSelectField'].includes(field.type)) {
            this.getOptionsEnums(field.name);
        }
    }

    getOptionsEnums = (selectfield_params) => {
            if (!selectfield_params) {
            this.setState({
                enum_options: []
            });
            return;
            }
            
            let data = {
            enum: selectfield_params.list_name,
            fieldDef: selectfield_params
            }

            fetch('/api/BLFlex/GetOptionsEnum/', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                "Accept": "application/json",
                // "X-CSRFToken": cookie.load('csrftoken'),
                "X-Auth-Token": getUserToken(),
                'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify(data)
            })
            .then(res => {
                return res.json()
            })
            .then(res => {
                if (res.status && res.data) {
                    this.setState({
                        enum_options: res.data
                    });
                    return res.data;
                }
            })
            .catch(error => {
                console.log("[getOptionsEnums] Error", error);
                this.setState({
                    enum_options: []
                });
                return [];
            });
        }

    render() {
        const { onHandleChange, field, isCreateEdit, section, index } = this.props;
        let filter_options = field.filter_options ? field.filter_options.map(x => { x["value"] = x["key"]; return x }) : [];
        let equal_from = null;
        let to = null;
        let filter_disabled = !isCreateEdit && (!field.allow_edit || field.allow_edit === 'False');

        const default_from_component = (<FormGroup>
            <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label>
            <Input disabled={filter_disabled} className="input-sm" bsSize="sm" type="text" name="equal_from" onChange={(e) => { onHandleChange("equal_from", e.target.value); }} value={field.equal_from} />
        </FormGroup>);

        const default_to_component = (<FormGroup>
            <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.To" /></Label>
            <Input disabled={filter_disabled} className="input-sm" bsSize="sm" type="text" name="to" onChange={(e) => { onHandleChange("to", e.target.value); }} value={field.to} />
        </FormGroup>);
    
        if (field.type === 'DateField' || field.type === 'DateTimeField' || field.type === 'TimeField') {

            if (date_fields_from_type_date.indexOf(field.selected_operator) > -1) {
                let date_value = null;
                try {
                    if (field.equal_from) {
                        if (isValidDate(field.equal_from)) {
                            date_value = field.equal_from;
                        } else {
                            date_value = moment(field.equal_from).toDate();
                        }
                    }

                } catch (error) { };

                if (field.type === 'DateField') {
                    equal_from = (<FormGroup>
                        <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label><br></br>

                        <DatePicker
                            selected={date_value}
                            onChange={(e) => { onHandleChange("equal_from", e); }}
                            dateFormat="dd/MM/yyyy"
                            className="form-control"
                            isClearable={true}
                            disabled={filter_disabled}
                        />
                    </FormGroup>);
                } else if (field.type === 'TimeField') {
                    equal_from = (<FormGroup>
                        <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label><br></br>

                        <DatePicker
                            selected={date_value}
                            onChange={(e) => { onHandleChange("equal_from", e); }}
                            showTimeSelect
                            showTimeSelectOnly
                            timeFormat="HH:mm:ss"
                            timeIntervals={15}
                            dateFormat="HH:mm:ss"
                            timeCaption="time"
                            className="form-control"
                            isClearable={true}
                            disabled={filter_disabled}

                        />
                    </FormGroup>);
                }else {
                    equal_from = (<FormGroup>
                        <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label><br></br>

                        <DatePicker
                            selected={date_value}
                            onChange={(e) => { onHandleChange("equal_from", e); }}
                            showTimeSelect
                            timeFormat="HH:mm"
                            timeIntervals={15}
                            dateFormat="dd/MM/yyyy HH:mm"
                            //locale="es"
                            timeCaption="time"

                            className="form-control"
                            isClearable={true}
                            disabled={filter_disabled}

                        />
                    </FormGroup>);
                }

                if (date_fields_to.indexOf(field.selected_operator) > -1) {
                    let date_value_to = null;
                    try {
                        if (field.to) {
                            if (isValidDate(field.to)) {
                                date_value_to = field.to;
                            } else {
                                date_value_to = moment(field.to).toDate();
                            }
                        }

                    } catch (error) { };

                    if (field.type === 'DateField') {
                        to = (<FormGroup>
                            <Label for="to"><ResourceViewer resourceTag="BLFlex.Label.To" /></Label><br></br>

                            <DatePicker
                                selected={date_value_to}
                                onChange={(e) => { onHandleChange("to", e); }}
                                dateFormat="dd/MM/yyyy"
                                className="form-control"
                                isClearable={true}
                                disabled={filter_disabled}
                            />
                        </FormGroup>);
                    } else if (field.type === 'TimeField') {
                        equal_from = (<FormGroup>
                            <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label><br></br>
    
                            <DatePicker
                                selected={date_value_to}
                                onChange={(e) => { onHandleChange("equal_from", e); }}
                                showTimeSelect
                                showTimeSelectOnly
                                timeFormat="HH:mm:ss"
                                timeIntervals={15}
                                dateFormat="HH:mm:ss"
                                timeCaption="time"
                                className="form-control"
                                isClearable={true}
                                disabled={filter_disabled}
    
                            />
                        </FormGroup>);
                    }else {
                        to = (<FormGroup>
                            <Label for="to"><ResourceViewer resourceTag="BLFlex.Label.To" /></Label><br></br>

                            <DatePicker
                                selected={date_value_to}
                                onChange={(e) => { onHandleChange("to", e); }}
                                showTimeSelect
                                timeFormat="HH:mm"
                                timeIntervals={15}
                                dateFormat="dd/MM/yyyy HH:mm"
                                //locale="es"
                                timeCaption="time"

                                className="form-control"
                                isClearable={true}
                                disabled={filter_disabled}
                            />
                        </FormGroup>);
                    }
                }
            } else if (date_fields_from_type_input.indexOf(field.selected_operator) > -1) {
                equal_from = (<FormGroup>
                    <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label><br></br>
                    <Input disabled={filter_disabled} className="input-sm" bsSize="sm" type="number" name="equal_from" onChange={(e) => { onHandleChange("equal_from", e.target.value); }} value={field.equal_from} />
                </FormGroup>);
            }
        } else if (field.type === 'BooleanField') {
            if (!['fk_not_in', 'fk_in', 'null_or_empty', 'not_empty', 'empty', 'current_user_code', 'current_record'].includes(field.selected_operator)) {
                equal_from = (<FormGroup>
                    <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label>
                    <Select isDisabled={filter_disabled} isMulti name="equal_from" options={boolean_options} onChange={(items) => { onHandleChange('equal_from', (items ? items.map((item) => { return item.value }) : [])) }} value={(typeof field.equal_from !== "undefined" ? boolean_options.filter((item) => { return field.equal_from.indexOf(item.value) !== -1 }) : null)} />
                </FormGroup>);
            }
        } else if (['SelectField', 'MultiSelectField'].includes(field.type)) {
            if (!['fk_not_in', 'fk_in', 'null_or_empty', 'not_empty', 'empty', 'current_user_code', 'current_record'].includes(field.selected_operator)) {
                
                let choices = this.state.enum_options? this.state.enum_options.map(x => { return { value: x.key, label: x.label } }) : [];
                equal_from = (<FormGroup>
                    <Label for="equal_from"><ResourceViewer resourceTag="BLFlex.Label.Equal/From" /></Label>
                    <Select isDisabled={filter_disabled} isMulti name="equal_from" options={choices} onChange={(items) => { onHandleChange('equal_from', (items ? items.map((item) => { return item.value }) : [])) }} value={(typeof field.equal_from !== "undefined" ? choices.filter((item) => { return field.equal_from.indexOf(item.value) !== -1 }) : null)} />
                </FormGroup>);
            }

        } else if (field.type === 'ForeignKey') {
            if (['not_equal', 'equal', 'fk_in', 'fk_not_in'].includes(field.selected_operator)) {
                const storeValue = field.equal_from;
                equal_from =
                    (<ForeignKey
                        fieldDef={field}
                        storeSection={section}
                        property="equal_from"
                        index={index}
                        name={field.name}
                        storeValue={storeValue}
                        selected_operator={field.selected_operator}
                        onChange={(e) => { onHandleChange("equal_from", e.target.value); }}
                        isDisabled={filter_disabled}

                    />)
            } else if (!['null_or_empty', 'not_empty', 'empty', 'current_user_code', 'current_record', 'current_user'].includes(field.selected_operator)) {
                equal_from = default_from_component;
            }
        } else {
            //CharField, TextField, FloatField, CurrencyField, IntergerField, DecimalField, EmailField, SmallIntergerField, ImageField, Filefield
            if (!['fk_not_in', 'fk_in', 'null_or_empty', 'not_empty', 'empty', 'current_user_code', 'current_record'].includes(field.selected_operator)) {
                equal_from = default_from_component;
            }
            if (field.selected_operator === 'between') {
                to = default_to_component;
            }
        }
        return (<Form>
            <FormGroup>
                <Label for="module"><ResourceViewer resourceTag="BLFlex.Label.Module" /></Label>
                <Input className="input-sm" bsSize="sm" type="text" name="module" disabled value={field.path} />
            </FormGroup>
            <FormGroup>
                <Label for="name"><ResourceViewer resourceTag="BLFlex.Label.Field" /></Label>
                <Input className="input-sm" bsSize="sm" type="text" name="name" disabled value={field.name} />
            </FormGroup>
            <FormGroup>
                <Label for="label"><ResourceViewer resourceTag="BLFlex.Label.Tag" /></Label>
                <Input disabled={!isCreateEdit} className="input-sm" bsSize="sm" type="text" name="label" onChange={(e) => { onHandleChange("label", e.target.value); }} value={field.label} />
            </FormGroup>
            <FormGroup>
                <Label for="type"><ResourceViewer resourceTag="BLFlex.Label.Type" /></Label>
                <Input className="input-sm" bsSize="sm" type="text" name="type" disabled value={field.type} />
            </FormGroup>
            <FormGroup>
                <Label for="aggregate"><ResourceViewer resourceTag="BLFlex.Label.Operation" /></Label>
                <Select isDisabled={filter_disabled} options={filter_options} onChange={(e) => { onHandleChange("selected_operator", e.value); }} value={filter_options.find((e) => { return e.value === field.selected_operator })} />
            </FormGroup>
            {field.selected_operator ? equal_from : null}
            {field.selected_operator ? to : null}
            <FormGroup check inline>
                <Label check>
                    Editable {' '}
                    <Input disabled={!isCreateEdit} type="checkbox" name="allow_edit" onChange={(e) => { onHandleChange("allow_edit", (e.target.checked ? 'True' : 'False')); }} checked={((field.allow_edit && typeof field.allow_edit === "boolean") || field.allow_edit === 'True') ? true : false} />
                </Label>
            </FormGroup>
        </Form>);
    }
}

export default FieldOptions;